

  .header{
    
    color: #fff;
    z-index: 999;
    
    border-bottom: 2px solid rgb(150, 158, 150);
    background-color: #24126A;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 13vh;
    margin-bottom: 10%;
    opacity: 0.9;
   overflow: hidden;
  }
  
  .container{
    background-color: #24126A;
    color: #fff;
  }

  .img{
      width: 200px;
      background-color: #24126A;
      color: #fff;
      z-index: 1000;
      margin-bottom: 2%;
  }
  .logo{
    background-color: #24126A;
    color: #fff;
  }
  
  .buttons{
    position:absolute;
   z-index: 1000;
   top:4%;
   left:80%;
   background-color: #24126A;


  }
  
  @media screen and(max-width:450px) {
    .header{
      display: flex;
    }
    
  }

  